.bg-container {
      background-color: #d4d2db;
      background-size: cover;
      height: 100vh;
      padding: 90px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .cashwithdrawal-inner-container {
      background-color: #150b3e;
      width: 60vh;
      border-radius: 25px;
      padding: 60px;
    }
    .head-section {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: center;
    }
    .main-heading {
      font-family: Roboto;
      font-size: 2rem;
      color: #c7d2fe;
    }
    .account-name-alphabet {
      height: 80px;
      width: 80px;
      border-radius: 40px;
      background-color: #c7d2fe;
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      color: #7c3aed;
      margin-right: 25px;
      font-weight: bold;
    }
    .rupees-section {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      color: white;
    }
    .your-balance-heading {
      font-family: Roboto;
      font-size: 2rem;
      color: #585076;
    }
    .amount {
      color: #c7d2fe;
      font-size: 2.6rem;
    }
    .amount-description {
      font-family: Roboto;
      font-size: 1.2rem;
      color: #585076;
      font-weight: bold;
    }
    .withdraw-heading {
      color: #c7d2fe;
      font-size: 2.5rem;
    }
    .choose-money-para {
      font-family: Roboto;
      font-size: 1.8rem;
      color: #585076;
    }
    .denomination-item-container {
      display: flex;
      flex-wrap: wrap;
    }
    